import { IdFormulario } from '@/dominio/editor/comum/IdFormulario';
import { TextoModelo } from '@/dominio/editor/comum/TextoModelo';
import { Titulo } from '@/dominio/editor/comum/Titulo';
import { ITextoEditavel, TextoEditavel } from './TextoEditavel';

export interface ITextoEditavelFactory {
    criar(
        id: IdFormulario,
        titulo: Titulo,
        textoTemplate: TextoModelo,
        indice: number,
    ): ITextoEditavel;
}

export class TextoEditavelFactory implements ITextoEditavelFactory {
    criar(
        id: IdFormulario,
        titulo: Titulo,
        textoTemplate: TextoModelo,
        indice: number,
    ) {
        return new TextoEditavel(id, titulo, textoTemplate, indice);
    }
}
